import {ref, query, orderByChild, orderByValue, child, limitToLast, get, onValue, onChildAdded , onChildChanged} from "firebase/database";
import usuario from "./usuario";
import { getDatabase } from "firebase/database";

// const consultarank = query(ref(database, '/'), orderByChild('modos'));

export default class ControleRank {
    
    constructor(firebase, auth){
      //Carregando bibliotecas instanciadas no app central
      this.auth = auth //Autenticaca: auth.auth (firebase auth)
      this.firebase = firebase

      //Carregando base de dados 
      this.db = getDatabase(firebase);

      //Carregando nó raiz
      this.database = ref(this.db, 'usuarios')

      //Controle
      this.controleRank = 0
      this.limiteRank = 5

      //Query de obtenção da lista de rank 
      this.consultarank = query(this.database, orderByChild('rank/pontuacao'), limitToLast(this.limiteRank));
      // this.consultarank = query(child(this.database,'william'));

      console.log("LOCALIZAÇÃO");
      console.log(child(this.database,'rank/pontuacao'));

      //Verificando autenticação -- Teste
      console.log("Autenticação teste -----");
      console.log(localStorage.getItem('auth'));
      console.log("USER", this.auth.getUser());
      console.log("------------------------");
      this.usuario = new usuario('Renato')
      if(this.auth.verifyUser())
        this.usuario = new usuario(this.auth.getUser().displayName)

        

      this.usuario = new usuario('Eduardo')
      this.listrank = []
    }

    // Conectando Pai a atualização do filho
    ancora(forceUpdate){
      this.forceUpdate = forceUpdate
    }
    update(item) {
      this.forceUpdate(item);
    }

    rank(){
      console.log("cade");
      onChildAdded(this.consultarank, (snapshot) => {
        console.log("Request rank: Sucesso");
        if (snapshot.exists()) {
          console.log("SUCESSO!");
          
          console.log("Lista rank request",snapshot.val());
          console.log(snapshot.key);
          // let aux = []
          // let item = {}
          // for(let i in snapshot.val()){
          //   item['nick'] = snapshot.val()[i]['nick']
          //   item['rank'] = snapshot.val()[i]['rank']


          //   aux.push(item)
          //   item = {}
          // }
            
          // this.listrank = aux
          let item = {}
          item['nick'] = snapshot.val()['nick']
          item['rank'] = snapshot.val()['rank']
          this.listrank.push(item)
          this.update(item)
        } else {
          console.log("VAzio");
          
        }
      }, {onlyOnce: true});
      console.log(this.consultarank.toJSON())
    }

    rankuser(){
      console.log("cade");
      onValue(this.consultarank, (snapshot) => {
        console.log("Request rank: Sucesso");
        if (snapshot.exists()) {
          console.log("SUCESSO!");
          
          console.log("Lista rank request",snapshot.val());
          console.log(snapshot.key);
          // let aux = []
          // let item = {}
          // for(let i in snapshot.val()){
          //   item['nick'] = snapshot.val()[i]['nick']
          //   item['rank'] = snapshot.val()[i]['rank']


          //   aux.push(item)
          //   item = {}
          // }
            
          // this.listrank = aux
          let item = {}
          item['nick'] = snapshot.val()['nick']
          item['rank'] = snapshot.val()['rank']
          this.listrank.push(item)
          this.update(item)
        } else {
          console.log("VAzio");
          
        }
      }, {onlyOnce: false});
      console.log(this.consultarank.toJSON())
    }


  
  
  
   
  }