import React from "react";
export default class View_Rank extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      spinner: false,
      listrank: [],
    };

  

    // Vinculo entre que força atualização entre os filhos, este componente, e o props.Jogo
    this.update = this.update.bind(this);

    this.props.serve.ancora(this.update);
    
  }

  update(elemento) {
    console.log(elemento);
    let aux = this.state.listrank
    aux.push(elemento)

    console.log("INSERINFO NO RANK VIEW", aux);
    this.setState({
      listrank: aux
    });
    // this.forceUpdate();
  }

    composicaoRank(){
      console.log("Lista Rank: No View");
      console.log(this.state.listrank);
      var users = [];
      for (let index = 0; index < this.state.listrank.length; index++) {
        users.push((
          <tr>
            <th scope="row">{index}</th>
            <td>{this.state.listrank[index].nick}</td>
            <td>{this.state.listrank[index].rank.pontuacao}</td>
            <td>{this.state.listrank[index].rank.tempo.minutes} : {this.state.listrank[index].rank.tempo.seconds}</td>
          </tr>
        ));
      }
      return React.createElement('tbody', { }, users);

    }
    render() {
    return (
  
      <div className="col d-flex flex-column align-items-center  text-center w-100 h-100 p-md-5 p-3">
          
          {
             
              console.log("RANKKKKKKKKKKKK", this.props.serve.listrank)
          }
        <h1>Placar de Jogadores</h1>
        <table class="table table-dark table-hover">
        <thead>
            <tr>
            <th scope="col">#</th>
            <th scope="col">Nickname</th>
            <th scope="col">Pontuação</th>
            <th scope="col">Tempo</th>
            </tr>
        </thead>
        {this.composicaoRank()}
        </table>
  
      </div>
    );
  }

}