import React from 'react';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import 'firebase/compat/auth';
import { getAuth, setPersistence, signInWithRedirect, browserSessionPersistence, inMemoryPersistence ,GoogleAuthProvider } from "firebase/auth";

const provider = new GoogleAuthProvider();





export default function SignInScreen(props) {

  function teste() {
    setPersistence(props.auth.auth, inMemoryPersistence)
    .then(() => {
      const provider = new GoogleAuthProvider();
      // In memory persistence will be applied to the signed in Google user
      // even though the persistence was set to 'none' and a page redirect
      // occurred.
      return signInWithRedirect(props.auth.auth, provider);
    })
    .catch((error) => {
      // Handle Errors here.
      const errorCode = error.code;
      const errorMessage = error.message;
    })
  
    return false
  }
  
    
    // Configure FirebaseUI.
    const uiConfig = {
      callbacks: {
        signInSuccessWithAuthResult: function(authResult, redirectUrl) {
          localStorage.setItem('auth',authResult );
        
          return false
        }
        
      },
    // Popup signin flow rather than redirect flow.
    signInFlow: 'popup',
    // Redirect to /signedIn after sign in is successful. Alternatively you can provide a callbacks.signInSuccess function.
    signInSuccessUrl: '/inicio',
    // We will display Google and Facebook as auth providers.
    signInOptions: [
      // List of OAuth providers supported.
      provider.providerId
    ],
  };

  return (
    <div>
      {/* {teste()} */}
      <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={props.auth.auth} />
    </div>
  );
}